import axios from "axios";

export default {
  getDeptos(city) {
    return axios.get(`/${city}`);
  },
  getDeptoInfo(depto, code) {
    return axios.get(`/lapaz/${depto}/calculator/${code}`);
  },
  getDashboardInfo(depto) {
    return axios.get(`/lapaz/${depto}/calculator`);
  },
  getBDInfo(depto) {
    return axios.get(`/lapaz/${depto}`);
  },
  editInfo(base, dias_sucios, extra_m3, depto){
    return axios.put(`/lapaz/admin/${depto}`,{
        base: base,
        dias_sucios: dias_sucios,
        extra_m3: extra_m3
    });
  },
  getBonos(depto) {
     return axios.get(`/lapaz/${depto}/bonos`);
  },
  getProduccion(depto){
    return axios.get(`/lapaz/${depto}/produccion`);
  }
};