import axios from "axios";

export default {
  getDeptos(city) {
    return axios.get(`/${city}`);
  },
  getDeptoInfo(depto, code) {
    return axios.get(`/juarez/${depto}/calculator/${code}`);
  },
  getDashboardInfo(depto) {
    return axios.get(`/juarez/${depto}/calculator`);
  },
  getBDInfo(depto) {
    return axios.get(`/juarez/${depto}`);
  },
  editInfo(base, dias_sucios, extra_m3, depto){
    return axios.put(`/juarez/admin/${depto}`,{
        base: base,
        dias_sucios: dias_sucios,
        extra_m3: extra_m3
    });
  },
};